.App {
  text-align: center;
  font-family: 'Titillium Web', sans-serif;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 60px;
}

.App-header {
  background-color: #222;
  height: 100px;
  padding: 15px;
}

.App-intro {
  font-size: large;
}

h1 {
  font-size: 40px;
  color: #51a7be;
}

h3 {
  font-size: 25px;
  margin-top: 20px;
}

.scoreboard-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 80vh;
}

.closed-container {
  color: #51a7be;
  font-family: 'Helvetica', 'Arial', sans-serif;
  font-size: 63px;
  font-weight: bold;
  text-align: center;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 5px solid #51a7be;
  margin-top: 30px;
}

.hit-container {
  color: #51a7be;
  font-family: 'Helvetica', 'Arial', sans-serif;
  font-size: 60px;
  font-weight: bolder;
  text-align: center;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-top: 38px;
}

.scoreboard-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
}

.row-number {
  font-size: 60px;
  margin: 30px 10px 0 10px;
}

.players-container {
  margin-top: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: row;
      flex-direction: row;
}

.players-container input {
  font-size: 25px;
  text-align: center;
  border-top: none;
  border-left: none;
  border-right: none;
}

.players-container p {
  font-size: 25px;
}

span {
  position: relative;
  bottom: 5px;
}

textarea {
  font-size: 20px;
  margin: 10px;
  border-radius: 5px;
}

textarea:focus, input:focus{
    outline: none;
}

@media screen and (max-width: 450px) {
  h1 {
    font-size: 30px;
  }
  h3 {
    font-size: 20px;
  }
  textarea {
    font-size: 13px;
  }
  .players-container input  {
    font-size: 15px;
  }
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



